import React, { useState, useEffect, useRef, useCallback } from "react";
import { Box, Typography, Button, TextField, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";
import Images from "../../../assets";

const steps = [
  {
    key: "signup",
    title: "Sign Up",
    description:
      "Sign up to take control of your study abroad journey  from discovering universities to getting expert help with applications and visas.",
    buttonText: "Start your journey now",
    leftImage: Images.ReactangleTab,
  },
  {
    key: "profile",
    title: "Create Profile",
    description:
      "Create your profile to get personalized university matches, track your applications, and receive expert support tailored to your goals.",
    buttonText: "Create Now",
    leftImage: Images.CreateTab
  },
  {
    key: "recommend",
    title: "Generate Recommendations",
    description:
      "Share your academic background, extracurriculars, and preferences to get personalized recommendations.",
    buttonText: "Generate Recommendation",
    leftImage: Images.GenerateTab
  },
];

const SignUpSection = () => {
  const navigate = useNavigate();
  const [phoneNumber, setPhoneNumber] = useState("");
  const [currentStep, setCurrentStep] = useState(0);
  const [scrollY, setScrollY] = useState(0);
  const [isHovered, setIsHovered] = useState(false);
  const autoPlayRef = useRef<NodeJS.Timeout | null>(null);
  
  const { title, description, buttonText, leftImage } = steps[currentStep];

  // Auto-play functionality
  useEffect(() => {
    const startAutoPlay = () => {
      if (!isHovered) {
        autoPlayRef.current = setInterval(() => {
          setCurrentStep((prevStep) => {
            const nextStep = (prevStep + 1) % steps.length;
            return nextStep;
          });
        }, 1000); // Change every 1 second
      }
    };

    const stopAutoPlay = () => {
      if (autoPlayRef.current) {
        clearInterval(autoPlayRef.current);
        autoPlayRef.current = null;
      }
    };

    if (isHovered) {
      stopAutoPlay();
    } else {
      startAutoPlay();
    }

    return () => {
      stopAutoPlay();
    };
  }, [isHovered]);

  // Handle scroll events
  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      setScrollY(scrollPosition);
      
      // Calculate which step to show based on scroll position
      const sectionHeight = window.innerHeight;
      const stepThreshold = sectionHeight * 0.5; // Change step when scrolled 50% of viewport height
      
      const newStep = Math.min(
        Math.floor(scrollPosition / stepThreshold),
        steps.length - 1
      );
      
      setCurrentStep(prevStep => {
        if (newStep !== prevStep) {
          return newStep;
        }
        return prevStep;
      });
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const nextStep = useCallback(() => {
    if (currentStep < steps.length - 1) {
      setCurrentStep(currentStep + 1);
    } else {
      // If it's the last step, navigate to startpage
      navigate("/startpage");
    }
  }, [currentStep, navigate]);

  const handleFormSubmit = useCallback(() => {
    navigate("/startpage");
  }, [navigate]);

  const handleMouseEnter = useCallback(() => {
    setIsHovered(true);
  }, []);

  const handleMouseLeave = useCallback(() => {
    setIsHovered(false);
  }, []);

  return (
    <Box
      sx={{
        width: "100%",
        minHeight: { xs: "auto", md: "100vh" },
        position: "relative",
        background: "white",
        overflow: "hidden",
      }}
    >
      {/* Background */}
      <Box
        sx={{
          width: "100%",
          height: { xs: "0%", md: "90%" },
          position: "absolute",
          background: "#F5EEE4",
          top: 0,
          left: 0,
        }}
      />

      {/* Main Flex Container */}
      <Box
        sx={{
          width: "100%",
          height: { xs: "auto", md: "100vh" },
          display: "flex",
          flexDirection: { xs: "column", md: "row" },
          justifyContent: { xs: "flex-start", md: "space-between" },
          alignItems: { xs: "center", md: "center" },
          position: "relative",
          zIndex: 1,
          padding: { xs: "20px", md: "40px 80px", lg: "40px 100px" },
          gap: { xs: 4, md: 8 },
        }}
      >
        {/* Dynamic Left Image - Flex Start */}
        <Box
          sx={{
            order: { xs: 2, md: 1 },
            display: "flex",
            justifyContent: { xs: "center", md: "flex-start" },
            alignItems: "center",
            flex: { xs: "0 0 auto", md: "0 0 auto" },
            width: { xs: "100%", md: "auto" },
            maxWidth: { xs: "85%", md: "45%" },
          }}
        >
          <Box
            component="img"
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            sx={{
              width: { xs: "100%", md: 400, lg: 500, xl: 600 },
              height: "auto",
              objectFit: "contain",
              transition: "opacity 0.3s ease-in-out",
              display: "block",
            }}
            src={leftImage}
            alt="Dynamic Content"
          />
        </Box>

        {/* Orange Shape Background with Content - Flex End */}
        <Box
          sx={{
            order: { xs: 1, md: 2 },
            position: "relative",
            display: "flex",
            justifyContent: { xs: "center", md: "flex-end", },
            alignItems: "center",
            flex: { xs: "0 0 auto", md: "0 0 auto" },
            width: { xs: "100%", md: "auto" },
            maxWidth: { xs: "100%", md: "50%" },
          }}
        >
          {/* Orange Shape Background */}
          <Box
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            sx={{
              width: { xs: "100%", md: 500, lg: 600, xl: 660 },
              height: { xs: 300, md: 450, lg: 410, xl: 450 },
              position: "relative",
              background: `url(${Images.SignUpBg})`,
              backgroundSize: "cover",
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              padding: { xs: 3, md: 4, lg: 5 },
            }}
          >
            {/* Content Container */}
            <Box
              sx={{
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "flex-start",
                gap: { xs: 2, md: 4, lg: 6 },
                display: "flex",
                width: "100%",
                maxWidth: { xs: "100%", md: 450, lg: 550 },
                padding: { xs: 2, md: 3 },
              }}
            >
              {/* Text Content */}
              <Box
                sx={{
                  width: "100%",
                  flexDirection: "column",
                  justifyContent: "flex-start",
                  alignItems: "flex-start",
                  gap: { xs: 1, md: 2 },
                  display: "flex",
                }}
              >
                <Typography
                  sx={{
                    color: "white",
                    fontSize: { xs: 24, sm: 28, md: 32, lg: 35, xl: 38 },
                    fontFamily: "Neutrif Studio",
                    fontWeight: "700",
                    wordWrap: "break-word",
                  }}
                >
                  {title}
                </Typography>
                <Typography
                  sx={{
                    color: "rgba(255, 255, 255, 0.90)",
                    fontSize: { xs: 14, sm: 15, md: 16, lg: 17, xl: 18 },
                    fontFamily: "Neutrif Studio",
                    fontWeight: "400",
                    lineHeight: { xs: "24px", sm: "28px", md: "32px", lg: "36px", xl: "40px" },
                    wordWrap: "break-word",
                    whiteSpace: "pre-wrap",
                  }}
                >
                  {description}
                </Typography>
              </Box>

              {/* Button */}
              <Button
                onClick={nextStep}
                sx={{
                  paddingLeft: { xs: 2, md: 2.5, lg: 3.375 },
                  paddingRight: { xs: 2, md: 2.5, lg: 3.375 },
                  paddingTop: { xs: 1, md: 1.25 },
                  paddingBottom: { xs: 1, md: 1.25 },
                  background: "white",
                  borderRadius: 4.25,
                  outline: "2px solid #F5EEE4",
                  justifyContent: "center",
                  alignItems: "center",
                  gap: 1.25,
                  display: "inline-flex",
                  textTransform: "none",
                  "&:hover": {
                    background: "#F8F9FA",
                  },
                }}
              >
                <Typography
                  sx={{
                    textAlign: "center",
                    color: "#495052",
                    fontSize: { xs: 14, sm: 15, md: 16, lg: 18, xl: 20 },
                    fontFamily: "Neutrif Studio",
                    fontWeight: "700",
                    wordWrap: "break-word",
                  }}
                >
                  {buttonText}
                </Typography>
              </Button>
            </Box>
          </Box>
        </Box>
      </Box>

      {/* Scroll Indicator (optional) */}
      {/* <Box
        sx={{
          position: "fixed",
          bottom: 20,
          right: 20,
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: 1,
          borderRadius: 1,
          fontSize: "0.75rem",
          zIndex: 1000,
        }}
      >
        Step {currentStep + 1} of {steps.length} | Auto: {!isHovered ? "ON" : "PAUSED"} | Scroll Y: {Math.round(scrollY)}
      </Box> */}
    </Box>
  );
};

export default SignUpSection; 