import React, { useRef, useCallback, useEffect, useState } from "react";
import { Box, Typography, Button, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";
import Images from "src/assets";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import { setPopup } from "src/store/Slices/popupSlice";
import { selectIsAuthenticated, selectRecommendations } from "src/store/selectors";

const HowItWorksSection = () => {
  const scrollRef = useRef<HTMLDivElement | null>(null);
  const scrollByCards = useCallback((direction: 'left' | 'right') => {
    const container = scrollRef.current;
    if (!container) return;
    const amount = Math.min(280, container.clientWidth * 0.8);
    container.scrollBy({ left: direction === 'right' ? amount : -amount, behavior: 'smooth' });
  }, []);
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const isAuthenticated = useAppSelector(selectIsAuthenticated);
  const recommendations = useAppSelector(selectRecommendations);
  const [pendingStepId, setPendingStepId] = useState<number | null>(null);

  const openLoginPopup = useCallback(() => {
    dispatch(
      setPopup({
        show: true,
        data: {
          container: {
            name: "login",
            dimensions: { width: "840px", height: "560px", show: "", close: "" },
          },
          type: "custom",
        },
      })
    );
  }, [dispatch]);

  const navigateForStep = useCallback((id: number) => {
    switch (id) {
      case 1:  navigate(`/explore`); break;
      case 2:  navigate(`/profile/personal`); break;
      case 3:  navigate(`/explore`); break;
      case 4:  navigate(`/profile/shortlisted`); break;
      default: break;
    }
  }, [navigate]);

  const handleStepClick = useCallback((id: number) => {
    try {
      if (!isAuthenticated) {
        setPendingStepId(id);
        openLoginPopup();
        return;
      }
      navigateForStep(id);
    } catch {
      setPendingStepId(id);
      openLoginPopup();
    }
  }, [isAuthenticated, openLoginPopup, navigateForStep]);

  useEffect(() => {
    if (isAuthenticated && pendingStepId !== null) {
      navigateForStep(pendingStepId);
      setPendingStepId(null);
    }
  }, [isAuthenticated, pendingStepId, navigateForStep]);

  const steps = [
    { id: 1, title: "Sign up",   description: "Start your journey in seconds. Sign up to explore more.", buttonText: "Start Now",      icon: Images.HowItWorks1 },
    { id: 2, title: "Profile",   description: "Create your profile. Get seen by global universities.",   buttonText: "Create Profile", icon: Images.HowItWorks2 },
    { id: 3, title: "Discover",  description: "Discover top universities. Find your match.",             buttonText: "Discover",       icon: Images.HowItWorks3 },
    { id: 4, title: "Apply",     description: "Apply in minutes. Chase your global dream.",             buttonText: "Start Now",      icon: Images.HowItWorks4 },
  ];

  return (
    <Box
      sx={{
        width: "100%",
        minHeight: { xs: "auto", sm: "350px", md: "400px" },
        flexDirection: "column",
        justifyContent: "space-between",
        alignItems: "center",
        display: "flex",
        background: "linear-gradient(to bottom, #70A6B2 0%, #70A6B2 50%, white 50%, white 100%)",
        // keep same spacing intention, add safe-area for iPhones
        py: {
          xs: "max(16px, calc(env(safe-area-inset-top) + 16px))",
          sm: 6,
          md: 8
        },
        position: "relative",
      }}
    >
      {/* Title (same position, improved fluid sizes but visually identical) */}
      <Typography
        sx={{
          alignSelf: "stretch",
          textAlign: "center",
          color: "white",
          fontSize: {
            xs: "clamp(28px, 7vw, 32px)",
            sm: "clamp(32px, 5.5vw, 40px)",
            md: "clamp(36px, 3.8vw, 48px)",
            lg: "clamp(44px, 3.2vw, 60px)"
          },
          fontFamily: "Neutrif Studio",
          fontWeight: 500,
          wordWrap: "break-word",
          px: { xs: 2, sm: 0 },
          pb:{xs:3,sm:4,md:5,lg:6}
        }}
      >
        How it Works?
      </Typography>

      {/* Steps Container  layout unchanged */}
      <Box
        sx={{
          width: "100%",
          maxWidth: "1400px",
          display: "flex",
          flexDirection: { xs: "row", md: "row" },
          justifyContent: { xs: "flex-start", md: "center" },
          alignItems: "center",
          gap: { xs: 2, sm: 3, md: 3 },
          position: "relative",
          top: { xs: 0, md: "-20px", lg: "-40px" },
          zIndex: 10,
          flexWrap: { xs: "nowrap", md: "wrap" },
          overflowX: { xs: "auto", md: "visible" },
          overflowY: "hidden",
          WebkitOverflowScrolling: "touch",
          scrollSnapType: { xs: "x mandatory", md: "none" },
          // hide scrollbar on mobile only; keeps desktop same
          scrollbarWidth: { xs: "none", md: "auto" } as any,
          "&::-webkit-scrollbar": { display: { xs: "none", md: "initial" } },
          margin: "0 auto",
          flex: 1,
          px: { xs: 2, sm: 3, md: 0 },
          mb: 3
        }}
        ref={scrollRef}
      >
        {/* Cards  exact structure retained */}
        {steps.map((step) => (
          <Box
            key={step.id}
            sx={{
              paddingLeft: { xs: 2, sm: 3 },
              paddingRight: { xs: 2, sm: 3 },
              paddingTop: { xs: 2, sm: 3 },
              background: "white",
              boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.25)",
              borderRadius: 4,
              flexDirection: "column",
              justifyContent: "flex-start",
              alignItems: "flex-start",
              gap: { xs: 2, sm: 3 },
              display: "flex",
              // keep same visual width, with tiny fluid bump for tiny phones
              width: { xs: "clamp(220px, 70vw, 240px)", sm: "260px", md: "280px" },
              maxWidth: { xs: "240px", md: "280px" },
              height: "auto",
              minHeight: { xs: "180px", sm: "200px" },
              flex: { xs: "0 0 auto", md: "0 0 280px" },
              scrollSnapAlign: { xs: "start", md: "none" },
              position: "relative",
              mx: { xs: 0.5, md: 0 },
              marginBottom: { xs: "20px" }
            }}
          >
            {/* Step Number */}
            <Typography
              sx={{
                position: "absolute",
                top: { xs: 12, sm: 16 },
                right: { xs: 12, sm: 16 },
                color: "#5C6163",
                fontSize: { xs: 36, sm: 42, md: 48 },
                fontFamily: "Neutrif Studio",
                fontWeight: "700",
                opacity: 0.15,
                zIndex: 2,
                lineHeight: 1,
              }}
            >
              {String(step.id).padStart(2, '0')}
            </Typography>

            {/* Content Section */}
            <Box
              sx={{
                alignSelf: "stretch",
                flexDirection: "column",
                justifyContent: "flex-start",
                alignItems: "flex-start",
                gap: 2,
                display: "flex",
                flex: 1,
              }}
            >
              {/* Icon and Title Row */}
              <Box
                sx={{
                  justifyContent: "flex-start",
                  alignItems: "center",
                  display: "inline-flex",
                  gap: 1.5
                }}
              >
                {/* Icon Container */}
                <Box
                  sx={{
                    width: { xs: 50, sm: 60, lg: 65 },
                    height: { xs: 50, sm: 60, lg: 65 },
                    position: "relative",
                    flexShrink: 0
                  }}
                >
                  <Box
                    sx={{
                      width: { xs: 50, sm: 60 },
                      height: { xs: 50, sm: 60 },
                      left: 0,
                      top: 0,
                      position: "absolute",
                      background: "white",
                      borderRadius: 4,
                    }}
                  />
                  <Box
                    component="img"
                    sx={{
                      width: { xs: 35, sm: 45, lg: 55 },
                      height: { xs: 40, sm: 50, lg: 55 },
                      left: { xs: 1, sm: 1.5, lg: 2.5 },
                      top: { xs: 1, sm: 1.5 },
                      position: "absolute",
                      objectFit: "contain"
                    }}
                    src={step.icon}
                    alt={step.title}
                    loading="lazy"
                    decoding="async"
                  />
                </Box>

                {/* Title */}
                <Typography
                  sx={{
                    mb: 1,
                    color: "#495052",
                    fontSize: { xs: 18, sm: 20, md: 24 },
                    fontFamily: "Neutrif Studio",
                    fontWeight: 500,
                    wordWrap: "break-word",
                  }}
                >
                  {step.title}
                </Typography>
              </Box>

              {/* Description */}
              <Typography
                sx={{
                  width: "100%",
                  color: "#5C6163",
                  fontSize: { xs: 14, sm: 16, md: 18 },
                  fontFamily: "Neutrif Studio",
                  fontWeight: 400,
                  lineHeight: { xs: "20px", sm: "22px", md: "24px" },
                  wordWrap: "break-word",
                }}
              >
                {step.description}
              </Typography>
            </Box>

            {/* Button and Arrow Section */}
            <Box
              sx={{
                justifyContent: "flex-start",
                alignItems: "center",
                gap: { xs: 4, sm: 6, md: 8 },
                display: "flex",
                width: "100%",
              }}
            >
              {/* Button Text */}
              <Typography
                sx={{
                  flex: 1,
                  justifyContent: "flex-end",
                  display: "flex",
                  flexDirection: "column",
                  color: "rgba(92, 97, 99, 0.40)",
                  fontSize: { xs: 14, sm: 15, md: 16 },
                  fontFamily: "Neutrif Studio",
                  fontWeight: 500,
                  wordWrap: "break-word",
                  cursor: "pointer",
                  "&:hover": { color: "#495052" },
                }}
                onClick={() => handleStepClick(step.id)}
              >
                {step.buttonText}
              </Typography>

              {/* Arrow Icon (hidden) */}
              <Box
                sx={{
                  width: 30,
                  height: 30,
                  padding: 2,
                  opacity: 0,
                  background: "#F48438",
                  borderRadius: 1,
                  flexDirection: "column",
                  justifyContent: "center",
                  alignItems: "center",
                  gap: 1,
                  display: "inline-flex",
                }}
              />
            </Box>
          </Box>
        ))}
      </Box>
    </Box>
  );
};

export default HowItWorksSection;
